<template>
	<div id="location-city">
		<div class="content">
			<div class="search-bar">
				<form action="/">
					<van-search
						v-model="key"
						placeholder="输入区域名进行搜索"
						shape="round"
						clearable
					>
						<div class="icon icon-left" slot="left" @click="onBack"></div>
					</van-search>
				</form>
			</div>
			<template v-if="!showSearch">
				<div class="now-city van-hairline--bottom" v-if="locationInfo.city">
					<div class="icon icon-locationfill"></div>
					<div class="margin-5-r">当前定位区域</div>
					<div>{{locationInfo.city}}</div>
				</div>
				<van-index-bar :index-list="indexList" :sticky-offset-top="54" z-index="1">
					<div class="hot-city" v-if="hots.length > 0">
						<div class="headline">热门区域</div>
						<van-row gutter="8">
							<van-col span="6" v-for="(item, index) in hots" :key="index">
								<div class="hot-item" @click="chooseLocation(item)">{{item.city}}</div>
							</van-col>
						</van-row>
					</div>
					<template v-for="(item, index) in list">
						<van-index-anchor :index="item.index">{{item.index}}</van-index-anchor>
						<van-cell :title="cityItem.city" v-for="(cityItem, cityIndex) in item.data" @click="chooseLocation(cityItem)" :key="cityIndex" />
					</template>
				</van-index-bar>
			</template>
			<template v-else>
				<van-cell-group v-if="searchCitys.length > 0">
					<van-cell :title="item.city" v-for="(item, index) in searchCitys" :key="index" @click="chooseLocation(item)" />
				</van-cell-group>
				<van-empty description="未搜索到对应区域" v-else />
			</template>

		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import { mapState, mapMutations } from "vuex"
	export default {
		data() {
			return {
				key: '',
				MapType: 'gaode',
				indexList: [],
				list: [],
				hots: [],
				showSearch: false,
				searchCitys: [],
				showPreLoading: true,
			}
		},
		watch: {
			key() {
				var that = this;
				let key= that.key;
				that.showSearch = key ? true : false;
				that.searchCitys = [];
				if(key) {
					that.util.request({
						url: 'wmall/home/newLocation/citySearch',
						data: {
							key: key
						}
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							return;
						}
						result = result.message;
						that.searchCitys = [...result.citys];
					})
				}
			}
		},
		methods: {
			...mapMutations(['setLocation', 'getLocation']),

			onLoad() {
				var that = this;
				that.util.request({
					url: 'wmall/home/newLocation/city',
				}).then((res) => {
					that.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, this.util.getUrl({path: '/pages/home/location'}), 1500, 'replace');
						return;
					}
					result = result.message;
					that.hots = [...result.hots];
					that.list = [...result.list];
					that.indexList = [...result.indexList];
				})
			},

			chooseLocation(item) {
				var that = this;
				var locationInfo = {
					last_location_x: that.locationInfo.location_x,
					address: item.address,
					location_x: item.location_x,
					location_y: item.location_y,
					city: item.city,
				}
				that.setLocation(locationInfo);
				that.$router.replace(that.util.getUrl({path: '/pages/home/location'}));
			},

			onBack() {
				this.$router.replace(this.util.getUrl({path: '/pages/home/location'}));
			}
		},
		computed: {
			...mapState([
				'locationInfo'
			]),
		},
		mounted() {
			this.getLocation();
			this.onLoad();
		},
	}
</script>

<style>
	#location-city .content{
		background-color: #fff;
	}
	#location-city .search-bar{
		position: sticky;
		top: 0px;
		z-index: 10;
	}
	#location-city .search-bar .icon-left{
		margin-right: 15px;
		font-size: 24px;
		font-weight: bold;
		color: #333;
	}
	#location-city .now-city{
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding: 16px;
		background-color: #fff;
		font-size: 14px;
	}
	#location-city .now-city .icon{
		font-size: 16px;
		margin-right: 5px;
	}
	#location-city .hot-city{
		background-color: #fff;
		padding: 0 16px 8px 16px;
	}
	#location-city .hot-city .headline{
		padding: 10px 0;
		color: #7a7a7a;
		font-size: 14px;
	}
	#location-city .hot-city .hot-item{
		width: 100%;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		background-color: #F7F7F7;
		padding: 8px 5px;
		text-align: center;
		border-radius: 4px;
		margin-bottom: 8px;
		font-size: 14px;
		color: #565656;
	}
</style>


